import Tan from "@/components/Tan.vue"
import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue'
export default {
	data() {
		return {
			tancl: false,
			shop: {},
			bulletin: null,
			salesData: [],
			totalData: {},
			unreadCount: null
		}
	},
	components: {
		Tan,
		uniNoticeBar
	},
	onShow() {
		let mid = this.getMerchantInfo().id;
		if(mid){
			//获取今日/昨日销售数据
			this.getTodayAndYesterdayData();
			//获取总订单数/总销售额
			this.getTotalCountAndPrice();
			setInterval(() => {
				this.getUnreadOrderCount();
			}, 1000 * 6)
		}
	},
	onLoad() {
		let mid = this.getMerchantInfo().id;
		if(!mid){
			uni.showToast({
				title:"您还未认证商家",
				icon:"none"
			})
		}
		//获取公告信息
		this.getBulletin();
	},
	methods: {
		getBulletin() {
			uni.request({
				url: this.prefix + "/getLastBulletin",
				success: (res) => {
					this.bulletin = res.data.data.content
				}
			})
		},
		//弹窗
		tan() {
			this.tancl = !this.tancl
		},
		getTodayAndYesterdayData() {
			uni.request({
				url: this.prefix + "/getTodayAndYesterdayData",
				data: {
					mid: this.getMerchantInfo().id
				},
				success: (res) => {
					this.salesData = res.data.data
				}
			})
		},
		getTotalCountAndPrice() {
			uni.request({
				url: this.prefix + "/getTotalCountAndPrice",
				data: {
					mid: this.getMerchantInfo().id
				},
				success: (res) => {
					this.totalData = res.data.data
				}
			})
		},
		getUnreadOrderCount() {
			uni.request({
				url: this.prefix + "/getUnreadOrderCount",
				data: {
					mid: this.getMerchantInfo().id
				},
				success: (res) => {
					this.unreadCount = res.data.data
					if (res.data.data > 0) {
						this.tancl = true;
					}
				}
			})
		},
		doRead() {
			uni.request({
				url: this.prefix + "/doRead",
				data: {
					mid: this.getMerchantInfo().id
				},
				success: (res) => {
					this.tancl = false;
					uni.navigateTo({
						url:"../tool/order_list"
					})
				}
			})
		},
		toBulletin(){
			uni.navigateTo({
				url:"../tool/bulletin"
			})
		}
	}
}
